<template>
  <div class="vx-scene">
    <vx-header :showreset="1" :title="$t('common.scene')" @reset='reset' :showback="1"></vx-header>
    <div class="vx-container">
      <vx-tcnbtn></vx-tcnbtn>
      <div class="vx-scene_list">
        <div class="vx-scene_item" :class="{selected:scene.id == selected}" v-for="(scene,index) in scenes" :key="scene.id" @click="selecteds(scene, index)">
          <div :class="{selecting:scene.id == selecting}"></div>
          <div class="no" :class="{online:scene.sceneStatus}">
            {{scene.id + 1}}
          </div>
          <div class="operator">
            <div class="edit" @click.stop="edit(scene)">
              <img src="../assets/common/edit.png" alt="">
            </div>
            <div class="source_name">{{scene.name}}</div>
          </div>
        </div>
      </div>
      <div class="operators">
        <div class="savescene" :class="{active: activeLoad}" @click="loadScene(scenes)" @touchstart="activeLoad=true" @touchend="activeLoad=false">
          <img v-if="activeLoad" src="../assets/common/load_pressed.png" height="20" width="20" slot="icon">
          <img v-else src="../assets/common/loads.png" height="20" width="20" slot="icon">
          <label>{{$t('scenes.loadScene')}}</label>
        </div>
        <div class="savescene" :class="{active: activeSave}" @click="save(scenes)" @touchstart="activeSave=true" @touchend="activeSave=false">
          <img v-if="activeSave" src="../assets/common/save_pressed.png" height="20" width="20" slot="icon">
          <img v-else src="../assets/common/save_scences.png" height="20" width="20" slot="icon">
          <label>{{$t('scenes.saveScene')}}</label>
        </div>
        <div class="savescene" :class="{active: activeClearOnce}" @click="deleteOnce(scenes)" @touchstart="activeClearOnce=true" @touchend="activeClearOnce=false">
          <img v-if="activeClearOnce" src="../assets/common/clearOnce_pressed.png" height="20" width="20" slot="icon">
          <img v-else src="../assets/common/clearOnce.png" height="20" width="20" slot="icon">
          <label>{{$t('scenes.deleteOne')}}</label>
        </div>
        <div class="savescene" :class="{active: activeClearAll}" @click="deleteAll(scenes)" @touchstart="activeClearAll=true" @touchend="activeClearAll=false">
          <img v-if="activeClearAll" src="../assets/common/clearAll_pressed.png" height="20" width="20" slot="icon">
          <img v-else src="../assets/common/clearAll.png" height="20" width="20" slot="icon">
          <label>{{$t('scenes.deleteAll')}}</label>
        </div>
      </div>
    </div>
    <!-- 修改名称组件 -->
    <vx-alert-message :msg="msg" :maxlength="20" v-if="showAlertMessage" @cancel='cancel' @sure='sure' :title="$t('common.updateName')"></vx-alert-message>
    <vx-alert-text v-if="showAlertText" @sureText='sureText' :message="$t(' scenes.inputError ')"></vx-alert-text>
    <!-- 载入场景组件 -->
    <vx-conform v-if="showConform" @cancelConform="cancelConform" @sureConform="sureConform" :message="loadSceneSure + '?'"></vx-conform>
    <vx-alert-text v-if="showAlertLoad" @sureText='sureLoad' :message="$t(' common.sceneError ')"></vx-alert-text>
    <!-- 保存场景组件 -->
    <vx-conform v-if="showLoadScene" @cancelConform="cancelsave" @sureConform="suresave" :message="$t('scenes.sure') + '?'"></vx-conform>
    <!-- 场景中已有信息时，保存是二次确认弹框 -->
    <vx-conform v-if="secondShowLoadScene" @cancelConform="secondaryCancel" @sureConform="SecondarySuresave" :message="SecondaryConfirmation"></vx-conform>
    <!-- 清空所有确认模态框 -->
    <vx-conform v-if="clearAll" @cancelConform="clearCance" @sureConform="clearSure" :message="$t('scenes.deleteAllMessage')"></vx-conform>
    <!-- 清空单个场景模态框 -->
    <vx-conform v-if="clearOnceScence" @cancelConform="clearCanceOnce" @sureConform="clearSureOnce" :message="clearOnceMessage"></vx-conform>
    <div class="model" v-show="model"></div>
  </div>
</template>

<script>
  import htp from "../api/http";
  import url from '../api/apiurl';
  import { Toast, Indicator } from 'mint-ui';
  import { getLoc } from 'src/utils';
  export default {
    name: 'scene',
    data() {
      return {
        msg: null, //场景名称
        SecondaryConfirmation: '', //覆盖场景文本
        loadSceneSure: '', //载入场景文本
        model: false,//模态层，配和loding使用
        clearOnceScence: false, //清楚单个场景模态框
        clearOnceMessage: '',//清除单个场景的模态框文本
        deviceName: '',
        scenes: [],
        selected: '',
        selecting: '',
        noStatus: '',
        selectIndex: '',
        showAlertMessage: false,
        showAlertText: false,
        showConform: false,
        showLoadScene: false,
        showAlertLoad: false,
        secondShowLoadScene: false,
        clearAll: false,
        activeLoad: false,
        activeSave: false,
        activeClearOnce: false,
        activeClearAll: false
      };
    },
    created() {
      this.deviceName = getLoc('device').deviceName;
      this.getScenes();
      this.getCurScene();
    },
    methods: {
      reset() {
        this.getScenes();
        this.getCurScene();
      },
      selecteds(scene, index) {
        this.selected = scene.id;
        this.selectIndex = index;
      },
      getCurScene() {
        htp.get(url.getCurScene).then(res => {
          if(res.status === 10000000) {
            this.selected = res.data.sceneCur;
            this.selecting = res.data.sceneCur;
          }
        });
      },
      getScenes() {
        htp.get(url.getSceneList).then(res => {
          if(res.status === 10000000) {
            this.scenes = res.data.sceneList;
          }
        });
      },
      //清空全部场景
      deleteAll(scenes) {
        this.clearAll = true;
      },
      //取消清空全部的场景
      clearCance() {
        this.clearAll = false;
      },
      //确认清空全部场景
      clearSure() {
        this.clearAll = false;

        if(!window, offline) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
          this.model = true;
        }


        htp.post(url.deleteScene, { sceneId: -1 }).then(res => {
          Indicator.close();
          this.model = false;
          if(res.status === 10000000) {
            this.scenes.forEach((v, i) => {
              v.sceneStatus = 0;
            });
            Toast(this.$t('scenes.deleteSuccess'));
          }
        });
      },
      //删除场景
      deleteOnce(scenes) {
        this.clearOnceMessage = this.$t('scenes.deleteScence') + ' ' + scenes[this.selected].name + '' + "?";
        if(scenes[this.selected].sceneStatus !== 1) {
          Toast(scenes[this.selected].name + ' ' + this.$t('scenes.empty'));
        } else {
          this.clearOnceScence = true;
        }
      },
      clearCanceOnce() {
        this.clearOnceScence = false;
      },
      clearSureOnce() {
        this.clearOnceScence = false;
        if(!window.offline) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
          this.model = true;
        }
        htp.post(url.deleteScene, { sceneId: this.selected }).then(res => {
          Indicator.close();
          this.model = false;
          if(res.status === 10000000) {
            this.scenes.forEach((v, i) => {
              if(v.id === this.selected) {
                v.sceneStatus = 0;
              }
            });
            Toast(this.$t('scenes.deleteSuccess'));
          }
        });
      },
      //载入场景
      loadScene: async function(scenes) {
        let noStatus = scenes.filter(v => {
          return v.sceneStatus == 0;
        });
        this.noStatus = noStatus; //保存无法载入的场景列表

        let flag = this.noStatus.some(item => item.id === this.selected);
        if(flag) {
          //选择的场景为空，无法载入;
          this.showAlertLoad = true;
        } else {
          //选择的场景可载入
          this.loadSceneSure = this.$t('scenes.loadSceneSure') + '  ' + scenes[this.selected].name + '';
          if(this.selected === this.selecting) {
            Toast('' + scenes[this.selected].name + ' ' + this.$t('scenes.loding'));
          } else {
            this.showConform = true;
          }
        }
      },
      //载入场景模态框组件事件
      sureConform() {
        this.showConform = false;
        if(!window.offline) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
          this.model = true;
        }

        this.setLoadScene().then(res => {
          Indicator.close();
          this.model = false;
          if(res.status === 10000000) {
            this.selecting = this.selected;
          }
          Toast(this.$t('scenes.loadSuccess'));
        });

      },
      //无法载入场景的模态框
      sureLoad() {
        this.showAlertLoad = false;
      },
      //载入场景方法
      setLoadScene: async function() {
        let params = {
          sceneId: this.selected
        };
        const res = await htp.post(url.setLoadScene, params);
        return res;
      },
      //保存场景
      save: async function(scenes) {
        //筛选已经保存过的场景
        var statusScenes = scenes.filter((v, i) => {
          return v.sceneStatus == 1;
        });
        var flag = statusScenes.some((v) => {
          return v.id === this.selected;
        });
        //如果选中的场景已经保存过场景，flag为true;
        if(flag) {
          this.SecondaryConfirmation = this.$t('scenes.SecondaryConfirmation') + ' ' + scenes[this.selected].name + '?';
          this.secondShowLoadScene = true;
        } else {
          // this.showLoadScene = true; //保存场景模态框出现
          if(!window.offline) {
            Indicator.open({
              text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
              spinnerType: 'triple-bounce'
            });
            this.model = true;
          }

          this.saveScene().then(res => {
            Indicator.close();
            this.model = false;
            if(res.status === 10000000) {
              this.scenes.forEach((v, i) => {
                if(v.id === this.selected) {
                  v.sceneStatus = 1;
                }
              });
              Toast(this.$t('scenes.saveSuccess'));
            }
          });
        }
      },
      //二次确认模态框取消按钮
      secondaryCancel() {
        this.secondShowLoadScene = false;
      },
      // 二次确认模态框确认按钮
      SecondarySuresave() {
        this.secondShowLoadScene = false;
        if(!window.offline) {
          Indicator.open({
            text: getLoc('lang') == 'en' ? 'Loading...' : '加载中...',
            spinnerType: 'triple-bounce'
          });
          this.model = true;
        }

        this.saveScene().then(res => {
          Indicator.close();
          this.model = false;
          if(res.status === 10000000) {
            this.scenes.forEach((v, i) => {
              if(v.id === this.selected) {
                v.sceneStatus = 1;
              }
            });
            Toast(this.$t('scenes.saveSuccess'));
          }
        });
      },
      //模态框确定，执行保存场景方法
      suresave() {
        this.showLoadScene = false;
        this.saveScene().then(res => {
          if(res.status === 10000000) {
            this.scenes.forEach((v, i) => {
              if(v.id === this.selected) {
                v.sceneStatus = 1;
              }
            });
            Toast(this.$t('scenes.saveSuccess'));
          }
        });
      },
      //取消保存场景
      cancelsave() {
        this.showLoadScene = false;
      },
      //保存场景方法
      saveScene: async function() {
        let params = {
          sceneId: this.selected
        };
        const res = await htp.post(url.saveScene, params);
        return res;
      },
      //修改名称方法
      edit(scene) {
        this.selected = scene.id;
        this.msg = scene.name;
        this.showAlertMessage = true;
      },
      //取消修改名称
      cancel() {
        this.showAlertMessage = false;
      },
      //子组件方法，用于保存用户输入的数据，并且正则匹配
      sure(val) {
        let re = /^[a-zA-Z0-9_\u4e00-\u9fa5\s]{1,20}$/;
        if(val) {
          if(!re.test(val)) {
            Toast(this.$t('scenes.inputError'));
          } else {
            this.showAlertMessage = false;
            htp.post(url.alterSceneName, { sceneId: this.selected, sceneName: val })
              .then(res => {
                if(res.status === 10000000) {
                  this.scenes.forEach((v, i) => {
                    if(v.id === this.selected) {
                      v.name = val;
                    }
                  });
                }
              });
          }
        } else {
          // this.showAlertMessage = false;
          Toast(this.$t('scenes.nameNull'));
        }
      },
      sureText() {
        this.showAlertText = false;
      },
      cancelConform() {
        this.showConform = false;
      }
    }

  };
</script>


  <style lang="less" scoped>
  .model {
    position: fixed;
    width: 100%;
    height: 100%;
    background: #000000;
    z-index: 30;
    opacity: 0.3;
  }
  .active {
    background: #2e2e34 !important;
  }
  .selecting {
    position: absolute;
    width: 25px;
    height: 25px;
    background: #fd7b00;
    border-radius: 50%;
    top: 15px;
    left: 15px;
  }
  .vx-scene {
    height: 100%;
    &_list {
      display: flex;
      flex-wrap: wrap;
      height: 85%;
      padding-bottom: 20px;
      overflow: auto;
      .vx-scene_item {
        position: relative;
        background-color: #6b7082;
        box-shadow: 2px 2px 5px #171719;
        border: 2px solid #212124;
        box-sizing: border-box;
        border-radius: 10px;
        // height: 400px;
        .no {
          text-align: center;
          color: #ddd;
        }
        .online {
          background-color: #3699f9;
          border-top-left-radius: 10px;
          border-top-right-radius: 10px;
        }
        .operator {
          border-top: 1px solid #2b2c33; /*no*/
          background-color: #40434c;
          height: 35%;
          width: 100%;
          border-bottom-left-radius: 10px;
          border-bottom-right-radius: 10px;
          display: flex;
          align-items: center;
          justify-content: space-between;
          padding-left: 15px;
          .source_name {
            padding: 0 15px;
            color: #a4a4a4;
            font-size: 14px; /*no*/
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            word-wrap: break-word;
            word-break: break-all;
          }
          .edit {
            // height: 60px;
            // line-height: 60px;
            // width: 50px;
            // position: absolute;
            // right: 0;
            // top: 0;
            img {
              width: 40px;
              height: 40px;
              vertical-align: middle;
            }
          }
        }
      }
      .selected {
        border: 2px solid #fd7b00; /*no*/
        border-radius: 10px;
      }
    }
    .operators {
      width: 100%;
      // margin: 30px 85px;
      display: flex;
      height: 12%;
      justify-content: space-around;
      border-top: 1px solid #b0b0b0; /*no*/
      padding: 20px 0 20px 0;
      position: fixed;
      bottom: 0;
      background: #18181b;
      box-sizing: border-box;
      .loadscene,
      .savescene {
        // flex: 1;
        width: 23%;
        // border: 1px solid #b0b0b0; /*no*/
        border-radius: 5px;
        text-align: center;
        // height: 70px;
        // line-height: 70px;
        padding: 15px 0;
        color: #ddd;
        background: #40424a;
        font-size: 14px; /*no*/
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        img {
          display: inline-block;
          vertical-align: middle;
          margin-bottom: 15px;
          width: 41px;
          height: 49px;
        }
        label {
          // padding-left: 20px;
        }
      }
    }
  }
  .mint-msgbox {
    border-radius: 4px;
    font-size: 21px;
  }
  .mint-msgbox-header {
    padding: 20px 0 0;
  }
  .mint-msgbox-title {
    font-size: 16px;
  }
  .mint-msgbox-content {
    padding: 13px 26px 20px;
    border-bottom: 1px solid #ddd; /*no*/
    min-height: 48px;
  }
  .mint-msgbox-btns {
    height: 53px;
    line-height: 53px;
  }
  .mint-msgbox-btn,
  .mint-msgbox-confirm {
    line-height: 46px;
  }

  /*竖屏模式*/
  @media screen and (orientation: portrait) {
    .vx-scene {
      &_list {
        margin: 35px 35px 0 35px;
        // padding: 0 60px;
        justify-content: space-between;
        .vx-scene_item {
          width: 31%;
          margin-bottom: 25px;
          height: 165px;
          .no {
            height: 65%;
            line-height: 130px;
            font-size: 16px; /*no*/
          }
        }
      }
    }
  }
  /*横屏模式*/
  @media screen and (orientation: landscape) {
    .vx-scene {
      &_list {
        // margin: 30px 35px;
        height: 80%;
        overflow: auto;
        display: flex;
        // justify-content: space-between;
        padding: 40px;
        .vx-scene_item {
          width: 22%;
          height: 200px;
          margin-right: 4%;
          margin-bottom: 25px;
          &:nth-child(4n) {
            margin-right: 0;
          }
          .no {
            height: 65%;
            line-height: 110px;
            font-size: 16px; /*no*/
          }
        }
      }
      .operators {
        width: 100%;
        // margin: 30px 85px;
        display: flex;
        height: 22%;
        justify-content: space-around;
        border-top: 1px solid #b0b0b0; /*no*/
        // padding: 25px 0 10px 0;
        position: fixed;
        bottom: 0;
        background: #18181b;
        box-sizing: border-box;
        .loadscene,
        .savescene {
          // flex: 1;
          width: 20%;
          // border: 1px solid #b0b0b0; /*no*/
          border-radius: 5px;
          text-align: center;
          // height: 70px;
          // line-height: 70px;
          padding: 15px 0;
          color: #ddd;
          background: #40424a;
          font-size: 14px; /*no*/
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          img {
            display: inline-block;
            vertical-align: middle;
            margin-bottom: 20px;
          }
          label {
            // padding-left: 20px;
          }
        }
      }
    }
  }
</style>


